<template>
<div > 
    <video id="myVideo" class="video-js" autoplay>
        <source src="../learn/cyfer.mp4" type="video/mp4">
    </video> 
</div> 
</template> 
<script> 
export default { 
    name: "LearnManage",
    mounted() { 
        this.initVideo();
    },
    methods: { 
        initVideo() { //初始化视频方法 
            let myPlayer = this.$video(myVideo, { //确定播放器是否具有用户可以与之交互的控件。没 有控件，启动视频播放的唯一方法是使用 autoplay 属性或通过 Player API。 
                controls: true, //自动播放属性,muted:静音播放 
                preload: "auto", //设置视频播放器的显示宽度（以像素为单位） width: "50px", //设置视频播放器的显示高度（以像素为单位） height: "50px", fluid: true
            });
        }
    }
}
</script>

